body {
  font-family: 'Google Sans', Roboto, 'Product Sans';
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #424242;
}

.main {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  width: 80vw;
  margin: auto;
}

.full-logo-container {
  border: 2px solid #CCCCCC;
  margin-bottom: 20px;
}

.scale-button>span {
  margin-left: 5px;
  margin-right: 5px;
  vertical-align: middle;
}

.scale-button {
  font-size: 1em;
}

@media only screen and (min-width: 768px) {
  .main {
    width: 90vw;
  }
}

@media only screen and (min-width: 1440px) {
  .main {
    width: 1200px;
  }
}

footer {
  margin-top: 10vh;
  width: 100%;
  text-align: center;
}